import React, { useCallback, useState } from "react";
import Switch from "@mui/material/Switch";
// import '../../common.css';
import "./BtxSwitch.css";

interface BtxSwitchProps {
  /**
   * Is this the principal call to action on the page?
   */
  primary?: boolean;
  /**
   * What background color to use
   */
  backgroundColor?: string;
  /**
   * How large should the label be?
   */
  size?: "small" | "medium" | "large";
  /**
   * BtxSwitch contents
   */
  label?: string;
  label1?: string | null;
  label2?: string | null;
  value?: any;
  /**
   * Optional change handler
   */
  onChange?: (e: any, checked: boolean) => void;
}

/**
 * Primary UI component for user interaction
 */
export const BtxSwitch = ({
  primary = false,
  size = "medium",
  backgroundColor,
  label,
  label1,
  label2,
  value,
  onChange,
  ...props
}: BtxSwitchProps) => {
  const mode = primary ? "btx-switch--primary" : "btx-switch--secondary";

  // const [checked, setChecked] = useState<boolean>(false);
  const handleChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>, checked: boolean) => {
      if (checked) {
        const a = "a";
      }
      if (onChange) {
        onChange(e, checked);
      }
    },
    [onChange]
  );

  return (
    <div>
      <label htmlFor="btx-switch">{label1}</label>
      <input type="checkbox" id="btx-switch" hidden />
      <Switch
        checked={value}
        onChange={handleChange}
        inputProps={{ "aria-label": "controlled" }}
      />
      <label htmlFor="btx-switch">{label2}</label>
    </div>
  );
};
